<template>
    <div>
        <audio
            ref="music"
            :src="mp3Url"
            @timeupdate="ontimeupdta"
            @durationchange="ondurationchange"
        ></audio>
        <el-row class="controls" :gutter="10">
            <el-col class="mb-1">
                <div class="audio-progress" @click="onprogressclick">
                    <el-progress
                        :text-inside="true"
                        :stroke-width="3"
                        :percentage="audioDataRef.progressPer"
                        :show-text="false"
                        status="success"
                    ></el-progress>

                    <span
                        class="progress-bar"
                        :style="{
                            left: audioDataRef.progressPer + '%'
                        }"
                        @mousedown="onmousedownprogress"
                    ></span>
                </div>
            </el-col>
            <el-col :span="3">
                <div @click="onAudioClick">
                    <i
                        v-if="audioDataRef.isPlay"
                        class="el-icon-video-pause"
                    ></i>
                    <i v-else class="el-icon-video-play"></i>
                </div>
            </el-col>
            <el-col :span="8">
                <div>
                    <span>
                        {{ audioDataRef.currentTime }}/{{
                            audioDataRef.totleTime
                        }}
                    </span>
                </div>
            </el-col>
            <el-col :span="10">
                <div ref="voice" class="voice-size" @click="onvoiceclick">
                    <el-progress
                        :text-inside="true"
                        :stroke-width="2"
                        :percentage="audioDataRef.voicePer"
                        :show-text="false"
                        status="success"
                    ></el-progress>
                    <span
                        :style="{
                            left: audioDataRef.voicePer + '%'
                        }"
                        @mousedown="onmousedownvoice"
                    ></span>
                </div>
                <div class="icon-voice" @click="onchangevolume">
                    <i
                        class="el-icon-turn-off-microphone"
                        v-if="audioDataRef.isSilence"
                    ></i>
                    <i class="el-icon-microphone" v-else></i>
                </div>
            </el-col>
            <el-col :span="3">
                <div>
                    <i class="el-icon-setting"></i>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue'

export default {
    props: {
        mp3Url: {
            type: String,
            required: true
        }
    },
    setup(props, context) {
        const audioDataRef = ref({
            progressPer: 0,
            voicePer: 50,
            voiceDefault: 50,
            isSilence: false,
            isPlay: false,
            currentTime: '',
            totleTime: ''
        })
        // video 
        const music = ref(null)
        const progress = ref(null)
        const voice = ref(null)

        let audioDom, progressDom, voiceSizeDom

        onMounted(() => {
            // audiodom
            audioDom = music.value

            // dom
            progressDom = progress.value
            voiceSizeDom = voice.value
        })

        /**
         * voiceSize 
         */
        const onvoiceclick = e => {
            setVolume(e)
        }

        //voice span
        const onmousedownvoice = e => {
            setVolume(e)
            window.onmousemove = function(e) {
                setVolume(e)
            }
            window.onmouseup = function() {
                window.onmousemove = false
            }
        }

        /**
         *  
         * 1. volume
         * 2. progress
         */
        function setVolume(e) {
            const rect = voiceSizeDom.getBoundingClientRect()
            let offset = e.clientX - rect.left
            //
            audioDataRef.value.isSilence = false
            // 
            if (offset < 0) {
                offset = 0
                // 
                audioDataRef.value.isSilence = true
            } else if (offset > rect.width) {
                offset = rect.width
            }

            // currentTime =  /  * 
            let currentVolume = offset / rect.width

            audioDom.volume = currentVolume //0--1
            audioDataRef.value.voicePer = currentVolume * 100
        }
        const onchangevolume = () => {
            //
            if (audioDataRef.value.isSilence) {
                //-->default
                audioDataRef.value.voicePer = audioDataRef.value.voiceDefault
            } else {
                audioDataRef.value.voicePer = 0
            }

            audioDom.volume = audioDataRef.value.voicePer / 100

            audioDataRef.value.isSilence = !audioDataRef.value.isSilence
        }

        /**
         * progress
         */
        const onprogressclick = e => {
            setCurrentTime(e)
        }

        /**
         * span
         * 1. , 
         * 2. 
         */
        const onmousedownprogress = function(e) {
            setCurrentTime(e)
            window.onmousemove = function(e) {
                setCurrentTime(e)
            }
            window.onmouseup = function() {
                window.onmousemove = false
            }
        }

        /**
         * :setCurrentTime
         *  
         */
        function setCurrentTime(e) {
            // currentTime =  /  * 
            const rect = progressDom.getBoundingClientRect()

            let offset = e.clientX - rect.left
            // 
            if (offset < 0) {
                offset = 0
            } else if (offset > rect.width) {
                offset = rect.width
            }

            let currentTime = (offset / rect.width) * audioDom.duration
            audioDom.currentTime = currentTime
        }

        /**
         * 
         * 1. durationundefined
         */
        const ondurationchange = () => {
            audioDataRef.value.currentTime = setTime(audioDom.currentTime)
            audioDataRef.value.totleTime = setTime(audioDom.duration)

            //
            audioDom.volume = audioDataRef.value.voicePer / 100
        }
        /**
         * 
         * 1. 
         * 2. 
         */
        const ontimeupdta = e => {
            let per = (audioDom.currentTime / audioDom.duration) * 100
            audioDataRef.value.progressPer = per
            audioDataRef.value.currentTime = setTime(audioDom.currentTime)
        }
        /**
         * 
         */
        function setTime(time) {
            let timeInt = parseInt(time)
            let minute = Math.floor(timeInt / 60) // 
            let second = timeInt - minute * 60

            if (minute < 10) {
                minute = '0' + minute
            }
            if (second < 10) {
                second = '0' + second
            }
            return minute + ':' + second
        }

        //
        const onAudioClick = () => {
            if (audioDataRef.value.isPlay) {
                audioDom.pause()
                audioDataRef.value.isPlay = !audioDataRef.value.isPlay
                return
            }
            audioDom.play()
            audioDataRef.value.isPlay = !audioDataRef.value.isPlay
        }

        return {
            audioDataRef,
            onAudioClick,
            ontimeupdta,
            onmousedownprogress,
            ondurationchange,
            onprogressclick,
            onmousedownvoice,
            onchangevolume,
            onvoiceclick,
            music,
            voice,
            progress
        }
    }
}
</script>

<style lang="scss" scope>
@import '@/assets/scss/home/audioControl.scss';
</style>
