<template>
    <div class="regist">
        <form
            action="#"
            id="regist-form"
            class="d-flex justify-content-center align-items-center flex-wrap"
        >
            <div class="username" data-field-container="username">
                <label for=""></label>
                <input
                    type="text"
                    name="username"
                    autocomplete="on"
                    data-field
                    value=""
                />
                <p data-field-error></p>
            </div>
            <div class="createpwd" data-field-container="createpwd">
                <label for=""> </label>
                <input
                    type="password"
                    name="createpwd"
                    autocomplete="off"
                    data-field-trigger="affirmpwd"
                    data-field
                    value=""
                />
                <p data-field-error></p>
            </div>
            <div class="affirmpwd" data-field-container="affirmpwd">
                <label for=""></label>
                <input
                    type="password"
                    name="affirmpwd"
                    autocomplete="off"
                    data-field-event="input"
                    data-field
                    data-field-trigger="createpwd"
                    value=""
                />
                <p data-field-error class=""></p>
            </div>
            <div class="other">
                <button @click="onRegist"></button>
                <div class="d-flex justify-content-end">
                    <span class="flex-grow-1">
                        xxx
                    </span>
                    <router-link
                        custom
                        to="/account/login"
                        v-slot="{ navigate }"
                        ><span role="link" @click="navigate"
                            >login</span
                        ></router-link
                    >
                    <router-link custom to="/home/mood" v-slot="{ navigate }"
                        ><span role="link" @click="navigate">home</span>
                    </router-link>
                </div>
                <h6 style="color: #2ed;">
                    demo
                </h6>
            </div>
        </form>
    </div>
</template>

<script>
import { ref } from 'vue'
import $http from '../../axios/http.js'
import useValidatorInstance from '../../compositions/account/useRegistValidatorInstance.js'
import { ElMessage } from 'element-plus'
import router from '@/routes/index.js'

export default {
    setup() {
        const validatorInstanceRef = ref(null) //
        useValidatorInstance(validatorInstanceRef) // onMounted,instancevalidator...
        // button-
        const onRegist = e => {
            e.preventDefault()

            const validator = validatorInstanceRef.value
            const formData = validator.getFormData()
            const isPass = validator.doValidatorForm()
            const data = {}
            for (const key in formData) {
                if (key === 'username') {
                    data[key] = formData[key][0]
                } else if (key === 'affirmpwd') {
                    data['pwd'] = formData[key][0]
                }
            }
            validator.setStatus()
            if (isPass.formPass) {
                $http
                    .post('/admin/account/regist', {
                        data: data
                    })
                    .then(res => {
                        ElMessage({
                            type: 'success',
                            message: '!'
                        })
                        router.push('/account/login')
                    })
            }
        }
        return {
            onRegist
        }
    }
}
</script>

<style lang="scss">
@import '../../assets/scss/account/regist.scss';
</style>
