<template>
    <el-row type="flex" class="nav--user" justify="end" align="middle">
        <el-col class="unlogin" :span="11" v-if="!$store.state.account.token">
            <router-link custom to="/account/login" v-slot="{ navigate }">
                <div class=" block">
                    <el-button @click="navigate" type="primary" size="mini"
                        ></el-button
                    >
                </div>
            </router-link>
        </el-col>
        <el-col class="islogin" :span="18" v-else>
            <div class="show d-flex align-items-center" @mouseenter="isShow">
                <div class="block " v-if="userInfo.photoUrl">
                    <el-avatar
                        :src="userInfo.photoUrl"
                        shape="square"
                        :fit="'fill'"
                        :size="30"
                    ></el-avatar>
                </div>
                <div class="block" v-else>
                    <el-avatar
                        :size="30"
                        fit="cover"
                        icon="el-icon-user-solid"
                    ></el-avatar>
                </div>

                <span>{{ userInfo.nickname }}</span>
                <i class="el-icon-caret-bottom"></i>
            </div>
            <div class="exhibition" @mouseleave="isHide" v-if="isExhibitionRef">
                <div class="top">
                    <p>
                        <i class="el-icon-s-home"></i>
                        <router-link
                            custom
                            v-slot="{ navigate, isExactActive }"
                            to="/user/mood-editor"
                            ><span
                                :class="{ 'exact-active': isExactActive }"
                                class="flex-shrink-0"
                                @click="navigate"
                                ></span
                            >
                        </router-link>
                    </p>
                    <div
                        class="d-flex justify-content-center align-items-center"
                    >
                        <div>
                            <span>0</span>
                            <span></span>
                        </div>
                        <div>
                            <span>0</span>
                            <span></span>
                        </div>
                        <div>
                            <span>0</span>
                            <span></span>
                        </div>
                    </div>

                    <div>
                        <el-button
                            type="primary"
                            icon="el-icon-s-promotion"
                            :round="true"
                            size="mini"
                            ></el-button
                        >
                    </div>
                </div>

                <div class="main">
                    <ul>
                        <li>
                            <i class="el-icon-user-solid"></i>

                            <router-link
                                custom
                                v-slot="{ navigate, isExactActive }"
                                to="/account/regist"
                                ><span
                                    :class="{ 'exact-active': isExactActive }"
                                    @click="navigate"
                                    ></span
                                >
                            </router-link>
                        </li>
                    </ul>
                </div>
                <div class="bottom">
                    <ul>
                        <li>
                            <i class="el-icon-user-solid"></i>
                            <span @click="onexist">
                                
                            </span>
                        </li>
                    </ul>
                </div>
            </div>
        </el-col>
    </el-row>
</template>

<script>
import { ref, computed } from 'vue'
import { useStore } from 'vuex'
import {
    GET_USER_INIT,
    ACCOUNT_LOGOUT,
    ACCOUNT_CHANGE
} from '@/store/variableNmae'

export default {
    setup() {
        const store = useStore()
        /**
         * user
         */
        let isExhibitionRef = ref(false)

        const isShow = () => {
            isExhibitionRef.value = true
        }
        const isHide = () => {
            isExhibitionRef.value = false
        }

        store.commit('account/' + ACCOUNT_CHANGE, {
            sessionStorage
        })
        /**
         * 
         */
        let userInfo = computed({
            //
            get() {
                return store.state.user.formInfo
            }
        })

        if (store.state.account.token) {
            store.dispatch('user/' + GET_USER_INIT)
        }

        const onexist = () => {
            store.commit('account/' + ACCOUNT_LOGOUT)
        }

        return {
            userInfo,
            isExhibitionRef,
            isShow,
            isHide,
            onexist
        }
    }
}
</script>

<style lang="scss" scope>
@import '@/assets/scss/home/homeUser.scss';
</style>
